        titulo: "Aparecida",
        descricao: `Considerado um dos bairros mais populosos da Cidade, com cerca de 50 mil habitantes, a Aparecida  delimitada por cinco grandes avenidas  Bartolomeu de Gusmo, Almirante Cochrane, Afonso Pena, Joaquim Montenegro e Pedro Lessa. Muitas so as referncias locais como a Fonte do Sapo, a Parquia Nossa Senhora Aparecida, que inspirou o nome do bairro, a Praa de Ces (Praa Caio Ribeiro de Moraes Silva), a FeirArte, o Sesc e o Praiamar Shopping.

  Quem ali reside ainda conta com equipamentos na rea da sade como a UPA Zona Leste, a Policlnica Aparecida e o Ambulatrio Mdico de Especialidades (AME), ligado ao Governo do Estado, alm das unidades municipais de educao Lourdes Ortiz e dos Andradas, e as estaduais Escolstica Rosa e Aristteles Ferreira e a CEEJA Maria Aparecida Pasqualeto Figueiredo.

  O bairro cresceu muito e possui uma das estruturas mais modernas da Cidade. Muita coisa mudou, ficou mais valorizado com a instalao de grandes empreendimentos que geraram emprego, mas ainda preserva chals com moradores antigos.

  A criao do bairro ocorreu a partir do Plano Diretor do Municpio de 1968, explica o historiador da Fundao Arquivo e Memria de Santos (Fams), Jos Dionsio de Almeida. "De 1867 at incio do sculo 20, a populao ainda se concentra no espao urbano que chamamos de Centro Histrico at o Paquet. Em seguida, gradativamente vai se formando o Macuco, que se efetiva a partir de 1915. Com o desmembramento desse bairro, cria-se a Aparecida, que leva o nome da igreja existente desde a dcada de 1930 para a de 1940".`,
